import { Component } from "react";



class Storage extends Component {

    constructor(props) {
        super(props)
        this.set = this.set.bind(this)
        this.get = this.get.bind(this)

    }



    async set(targetName, data) {

        try {
            await localStorage.setItem(targetName, JSON.stringify(data));
        } catch (ex) {
            console.log('AsyncStorage error: ' + ex.message);
        }

        return;
    }

    async get(targetName) {
        let data;
        try {
            data = await localStorage.getItem(targetName);
        } catch (ex) {
            console.log('AsyncStorage error: ' + ex.message);
        }

        return data;
    }

    async delete(targetName) {

        try {
            await localStorage.removeItem(targetName)
        } catch (ex) {
            console.log('AsyncStorage error: ' + ex.message);
        }
        return;
    }

    render() {
        return null
    }
}

const db = new Storage()
export default db